import { css } from '@emotion/react';
import { observer } from 'mobx-react-lite';
import React, { useEffect, useRef } from 'react';
import Card from './components/Card';
import { useCardStore } from './hooks/useStore';
import listImg from './img/list.png';

const Cards = () => {
  const { cards } = useCardStore();
  const listRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    console.log(listRef.current?.offsetHeight);
  }, []);

  return (
    <div css={styleList()}>
      <div ref={listRef}>
        {cards.map(({ id }, index) => {
          return <Card key={id} id={id} index={index} />;
        })}
      </div>
    </div>
  );
};

export default observer(Cards);

function styleList() {
  return css`
    background-image: url(${listImg});
    background-size: cover;
    background-repeat: no-repeat;
    margin-top: -2rem;
    margin-left: -2rem;
    margin-right: -20px;

    > div {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(calc(140px - 1rem), 1fr));
      padding: 9rem 6rem;
      justify-items: center;
    }
  `;
}
