import { action, makeObservable, observable } from 'mobx';
import RootStore from './RootStore';
import { initialGuideList } from 'src/data';

export type Guide = {
  label: string;
  text: string;
};

class GuideStore {
  rootStore: RootStore;

  guides: Guide[];

  constructor(rootStore: RootStore) {
    makeObservable(this, {
      guides: observable,
      setGuides: action,
    });
    this.rootStore = rootStore;
    this.guides = initialGuideList;
  }

  getGuide = (index: number) => this.guides[index];

  setGuides = (guides: Guide[]) => {
    this.guides = guides;
  };
}

export default GuideStore;
