import CardStore from './CardStore';
import GlobalStore from './GlobalStore';
import GuideStore, { Guide } from './GuideStore';
import { Card } from 'src/model/Card';
import { stringifyPretty } from 'src/utils';

export type IState = {
  cards: string[];
  guides: Guide[];
};

const DATA_KEY = 'data';

class RootStore {
  globalStore: GlobalStore;

  cardStore: CardStore;

  guideStore: GuideStore;

  constructor() {
    this.globalStore = new GlobalStore(this);
    this.cardStore = new CardStore(this);
    this.guideStore = new GuideStore(this);
  }

  printState = () => {
    const rootState: IState = {
      cards: this.cardStore.cards.map(({ description }) => description),
      guides: this.guideStore.guides,
    };

    navigator.clipboard.writeText(stringifyPretty(rootState));
  };

  loadData = (jsonString: string) => {
    try {
      const json: IState = JSON.parse(jsonString);
      const { cards, guides } = json;

      const typeCheckCard = cards.every((v) => isCard(v));
      if (!typeCheckCard) throw Error('card type error');
      const typeCheckGuide = guides.every((v) => isGuide(v));
      if (!typeCheckGuide) throw Error('guide type error');

      this.cardStore.setCards(
        cards.map((text, idx) => {
          return new Card({
            id: idx,
            title: `Q. ${idx + 1}`,
            description: `${text}`,
            flipped: false,
          });
        }),
      );
      this.guideStore.setGuides(guides);
      localStorage.setItem(DATA_KEY, jsonString);
    } catch (error) {
      alert('음... 파일이 뭔가 이상해요');
      this.initLocalStorage();
      console.log(error);
    }
  };

  initData = () => {
    try {
      const jsonString = localStorage.getItem(DATA_KEY)!;
      if (!jsonString) return;
      this.loadData(jsonString);
    } catch (error) {
      console.log(`로컬 스토리지가 없거나 이상함`);
    }
  };

  initLocalStorage = () => {
    localStorage.removeItem(DATA_KEY);
  };
}

export default RootStore;

function isCard(o: any): o is string {
  return typeof o === 'string';
}
function isGuide(o: any): o is Guide {
  return 'label' in o && 'text' in o;
}
