import { css } from '@emotion/react';
import { observer } from 'mobx-react-lite';
import React, { useCallback, useState } from 'react';
import closeIcon from '../img/close.png';
import CardBack from './CardBack';
import Modal, { styleModalContent } from './Modal';
import { useCardStore, useGlobalStore } from 'src/hooks/useStore';
import { buttonBox } from 'src/style';

type Props = {
  id: number;
  index: number;
};

const Card = ({ id, index }: Props) => {
  const { getCard, isDone } = useCardStore();
  const { confetti } = useGlobalStore();
  const { description, flipped, title, toggle } = getCard(id);

  const [isOpen, setIsOpen] = useState(false);

  const onClickHandler = useCallback(() => {
    toggle();

    if (flipped) return;
    setIsOpen(true);
  }, [flipped, toggle]);

  const onCloseHandler = () => {
    setIsOpen(false);
    if (isDone) confetti();
  };

  return (
    <>
      <div
        key={id}
        css={styleCard(flipped)}
        aria-hidden="true"
        onClick={onClickHandler}
      >
        {!flipped && <CardBack index={index} />}

        {flipped && (
          <div>
            <h1>🗸</h1>
          </div>
        )}
      </div>

      <Modal isOpen={isOpen} onClose={onCloseHandler}>
        <div css={styleModalContent}>
          <h2>{title}</h2>
          <p
            css={css`
              white-space: pre-wrap;
            `}
          >
            {description}
          </p>
          <button type="button" onClick={onCloseHandler}>
            <img src={closeIcon} alt="x" />
          </button>
        </div>
      </Modal>
    </>
  );
};

export default observer(Card);

function styleCard(flipped: boolean) {
  return css`
    padding: 0.1rem;
    border: 2px solid #ffffff;
    border-radius: 0.5rem;
    background-color: #0050fb;
    color: white;
    aspect-ratio: 0.67 / 1;
    width: 110%;
    ${buttonBox}

    transition: 0.2s ease;

    ${!flipped &&
    css`
      
      :hover {
        transform: scale(1.2);
        background-color: #0028c7;
        z-index: 1;
        `}

    ${flipped &&
    css`
      font-size: 1.5rem;
      background-color: #000;
      font-size: 1rem;
    `}
  `;
}
