import { css } from '@emotion/react';
import { observer } from 'mobx-react-lite';
import React, { useCallback, useState } from 'react';
import closeIcon from '../img/close.png';
import Modal, { styleModalContent } from './Modal';
import { useGuideStore } from 'src/hooks/useStore';
import { buttonBox, styleButton } from 'src/style';

type GuideProps = {
  index: number;
};

const Guide = ({ index }: GuideProps) => {
  const { getGuide } = useGuideStore();
  const { label, text } = getGuide(index);

  const [isOpen, setIsOpen] = useState(false);

  const onClickHandler = useCallback(() => {
    setIsOpen(true);
  }, []);

  const onCloseHandler = () => setIsOpen(false);

  return (
    <>
      <div
        css={css`
          ${buttonBox}
          ${styleButton}
        `}
        aria-hidden="true"
        onClick={onClickHandler}
      >
        {label}
      </div>

      <Modal isOpen={isOpen} onClose={onCloseHandler}>
        <div css={styleModalContent}>
          <h2>
            가이드 {index + 1}. {label}
          </h2>
          <p>{text}</p>
          <button type="button" onClick={onCloseHandler}>
            <img src={closeIcon} alt="x" />
          </button>
        </div>
      </Modal>
    </>
  );
};

export default observer(Guide);
