import { css } from '@emotion/react';
import { observer } from 'mobx-react-lite';
import React from 'react';
import key from 'weak-key';
import Guide from './Guide';
import { useGuideStore } from 'src/hooks/useStore';

const Guides = () => {
  const { guides } = useGuideStore();
  return (
    <div
      css={css`
        display: grid;
        grid-template-columns: auto 1fr;
        font-size: 1.5rem;
        font-weight: 700;
      `}
    >
      <div
        css={css`
          margin-top: 3px;
        `}
      >
        가이드
      </div>
      <div>
        {guides.map((v, index) => {
          return <Guide key={key(v)} index={index} />;
        })}
      </div>
    </div>
  );
};

export default observer(Guides);
