import { action, computed, makeObservable, observable } from 'mobx';
import RootStore from './RootStore';
import { initialCardList } from 'src/data';
import { Card } from 'src/model/Card';
import { shuffle } from 'src/utils';

class CardStore {
  rootStore: RootStore;

  cards: Card[];

  constructor(rootStore: RootStore) {
    makeObservable(this, {
      cards: observable,
      setCards: action,
      cardsMap: computed,
    });
    this.rootStore = rootStore;
    this.cards = initialCardList.map((text, idx) => {
      return new Card({
        id: idx,
        title: `Q. ${idx + 1}`,
        description: `${text}`,
        flipped: false,
      });
    });
  }

  flipAll = () => {
    this.cards.forEach((card) => card.flip());
  };

  unflipAll = () => {
    this.cards.forEach((card) => card.unflip());
  };

  setCards = (cards: Card[]) => {
    this.cards = cards;
  };

  shuffle = () => {
    this.setCards(shuffle(this.cards));
  };

  getCard = (id: number): Card => this.cardsMap[id];

  get cardsMap() {
    return this.cards.reduce((p, c) => {
      const { id } = c;
      return {
        ...p,
        [id]: c,
      };
    }, {} as Record<number, Card>);
  }

  get isDone() {
    return this.cards.every(({ flipped }) => flipped);
  }
}

export default CardStore;
