import { action, makeObservable, observable } from 'mobx';

class Card {
  id: number;

  title: string;

  description: string;

  flipped: boolean;

  constructor({ description, flipped, id, title }: Partial<Card>) {
    makeObservable(this, {
      description: observable,
      flipped: observable,
      title: observable,

      toggle: action,
      flip: action,
      unflip: action,
    });

    this.id = id ?? -1;
    this.title = title ?? '';
    this.description = description ?? '';
    this.flipped = flipped ?? false;
  }

  toggle = () => {
    const { flipped } = this;
    this.flipped = !flipped;
  };

  flip = () => {
    this.flipped = true;
  };

  unflip = () => {
    this.flipped = false;
  };
}

export { Card };
