import { useContext } from 'react';
import { StoreContext } from 'src/contexts';
import RootStore from 'src/store/RootStore';

export const useGlobalStore = () => {
  return useContext<RootStore>(StoreContext).globalStore;
};

export const useCardStore = () => {
  return useContext<RootStore>(StoreContext).cardStore;
};

export const useGuideStore = () => {
  return useContext<RootStore>(StoreContext).guideStore;
};

export const useStore = () => {
  return useContext<RootStore>(StoreContext);
};
