import { css } from '@emotion/react';
import React, { ReactNode, useEffect, useMemo, useRef } from 'react';
import { createPortal } from 'react-dom';

type PortalProps = {
  children: ReactNode;
};

const MODAL_ID = 'modal';

const Portal = ({ children }: PortalProps) => {
  const rootElement = useMemo(() => document.getElementById(MODAL_ID)!, []);

  return createPortal(children, rootElement);
};

type ModalProps = {
  isOpen: boolean;
  onClose: () => void;
  children: ReactNode;
};

const Modal = ({ isOpen, onClose, children }: ModalProps) => {
  const background = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const { current } = background;
    if (!current || !isOpen) return () => null;

    const keyHandler = (e: KeyboardEvent) => e.key === 'Escape' && onClose();
    const clickHandler = (e: MouseEvent) => e.target === current && onClose();

    current.addEventListener('click', clickHandler);
    window.addEventListener('keyup', keyHandler);

    return () => {
      current.removeEventListener('click', clickHandler);
      window.removeEventListener('keyup', keyHandler);
    };
  }, [isOpen, onClose]);

  if (!isOpen) return <></>;

  return (
    <Portal>
      <div css={styleModal(isOpen)} ref={background}>
        <div>{children}</div>
      </div>
    </Portal>
  );
};

export default Modal;

function styleModal(isOpen: boolean) {
  return css`
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(51, 51, 51, 0.3);
    backdrop-filter: blur(2px);
    z-index: 20;

    > div {
      box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
      background-color: white;
      border-radius: 1rem;
      opacity: 0;
      margin: 1rem 2rem;

      ${isOpen &&
      css`
        background-color: white;
        opacity: 1;
      `}
    }
  `;
}

export const styleModalContent = css`
  padding: 2rem;
  font-size: 2rem;
  width: 60vw;
  aspect-ratio: 1.71 / 1;

  display: grid;
  grid-template-rows: auto 1fr auto;

  /* modal header */
  > :first-of-type {
    margin-bottom: 2rem;
  }

  > button {
    text-align: center;
    border-radius: 50%;
    width: 45px;
    margin: auto;
    color: transparent;

    > img {
      width: 100%;
    }
  }
`;
